import React from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';
import './CoffeeSelect.css';


function CoffeeSelect ({changedMenu}){
    return (
        <FormGroup row>
        <Col xs={1}/>
        <Col xs={3}>
          <Label for="coffeeSelect"> :</Label>
        </Col>
        <Col xs={7}>
           <Input type="select" name="select" id="coffeeSelect"  onChange={changedMenu}>
             <option value=""> </option>
             <option></option>
             <option></option>
             <option></option>
             <option></option>
             <option></option>
           </Input>
         </Col>
         </FormGroup>
    );
 }

export default CoffeeSelect;



